.nav {
  display: block;

  &__list {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-right: 20px;
    height: $nav-item-height;
  }

  &__video {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  &__link {
    padding: 23px 0;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: $nav-line-height;
    color: $main-accent-color;
    text-decoration: none;
    text-transform: uppercase;
    transition: color 0.5s;

    &:hover {
      color: $blue-accent-color;
      transition: 0.5s;
    }
  }
}

.is-active {
  position: relative;
  color: $blue-accent-color;

  &::after {
    position: absolute;
    display: block;
    bottom: 0;
    content: "";
    width: 100%;
    height: 4px;
    background-color: $blue-accent-color;
    border-radius: 8px;
  }
}
